import { useForm } from "react-hook-form";
import { useRecoilState, useSetRecoilState } from "recoil";
import { categoryState, currentCategoryState } from "../atoms";
import styled from "styled-components";

interface IForm {
  category: string;
}

const FlexBetweenBox = styled.div`
  display: flex;
  justify-content: space-between;
`;

const Title = styled.h1`
  font-weight: bold;
  font-size: 28px;
  margin-top: 20px;
  margin-bottom: 5px;
`;

const Categories = styled.div`
  border: 1px solid gray;
  border-radius: 5px;
  margin-bottom: 25px;
  padding: 10px 15px;
`;

const Button = styled.button`
  background-color: #171717;
  margin-right: 5px;
  margin-bottom: 5px;
  padding: 5px 10px;
  color: #f1f1f1;
`;

function CategoryList() {
  const [categories, setCategories] = useRecoilState(categoryState);
  const setCategory = useSetRecoilState(currentCategoryState);
  const { register, handleSubmit, setValue } = useForm<IForm>();

  const handleValid =({ category }: IForm) => {
    setCategories((oldCategories) => [
      {title: category, id:  Date.now()},
      ...oldCategories,      
    ]);

    setCategory(category);
    setValue("category", "");
  }

  const onClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    const {
      currentTarget: {name},
    } = event;

    setCategory(name);
  }

  return (
    <>
      <Title>Categories</Title>
      <Categories>
        <form onSubmit={handleSubmit(handleValid)} style={{ marginBottom: "10px" }}>
          <FlexBetweenBox>
            <input
              {...register("category", {
                required: "Please write Category",
              })}
              placeholder="Write a category"
            />
            <button>Add</button>
          </FlexBetweenBox>
        </form>
        <div>
          {
            categories?.map((cat) => (
              <Button key={cat.id} name={cat.title} onClick={onClick}>{cat.title}</Button>
            ))
          }
        </div>
      </Categories>
    </> 
  );
}

export default CategoryList;