import { atom, selector } from "recoil";

// localStorage 사용을 위한 함수
// https://recoiljs.org/ko/docs/guides/asynchronous-data-queries
export const localStorageEffect = (key:string) => ({setSelf, onSet}: any) => {
  const savedValue = localStorage.getItem(key);
  if (savedValue !== null) {
    setSelf(JSON.parse(savedValue));
  }

  onSet((newValue: any, _: any, isReset: boolean) => {
    isReset
      ? localStorage.removeItem(key)
      : localStorage.setItem(key, JSON.stringify(newValue));
  });
}

export interface ICategory {
  id: number;
  title: string;
}

export interface IToDo {
  text: string;
  id: number;
  category: string;
}

export const currentCategoryState = atom<string>({
  key: "currentCategory",
  default: "",
  effects: [
    localStorageEffect("currentCategory")
  ],
});

export const categoryState = atom<ICategory[]>({
  key: "category",
  default: [],
  effects: [
    localStorageEffect("category")
  ],
});

export const toDoState = atom<IToDo[]>({
  key: "toDo",
  default: [],
  effects: [
    localStorageEffect("toDo"),
  ]
});

export const toDoSelector = selector({
  key: "toDoSelector",
  get: ({ get }) => {
    const toDos = get(toDoState);
    const category = get(currentCategoryState);

    return toDos.filter((toDo) => toDo.category === category);
  }
});