
import { useEffect } from "react";
import { useRecoilState, useRecoilValue } from "recoil";
import { currentCategoryState, categoryState, toDoSelector } from "../atoms";
import CreateToDo from "./CreateToDo";
import ToDo from "./ToDo";
import styled from "styled-components";

const Title = styled.h1`
  font-weight: bold;
  font-size: 28px;
  margin-bottom: 5px;
`;

function ToDoList() {
  const [categories, setCategoris] = useRecoilState(categoryState);
  const toDos = useRecoilValue(toDoSelector);
  const [ category, setCategory ] = useRecoilState(currentCategoryState);
  const onInput = (event: React.FormEvent<HTMLSelectElement>) => {
    setCategory(event.currentTarget.value as any);
  };

  useEffect(() => {
    if (categories?.length === 0) {
      setCategoris([
        {id: Date.now()-2, title: "TO DO"},
        {id: Date.now()-1, title: "DOING"},
        {id: Date.now(), title: "DONE"},
      ]);

      setCategory("TO DO");
    }
  }, []);

  return (
    <div>
      <Title>To Dos</Title>
      <select value={category} onInput={onInput}>
        {          
          categories?.map((cat) => (
            <option key={cat.id} value={cat.title}>{cat.title}</option>
          ))
        }
      </select>
      <CreateToDo />
      <ul>
        {toDos.map((toDo) => (
          <ToDo key={toDo.id} {...toDo} />
        ))}
      </ul>
    </div>
  );
}

export default ToDoList;