import { useRecoilValue, useSetRecoilState } from "recoil";
import { 
  categoryState, 
  currentCategoryState, 
  ICategory, 
  IToDo, 
  toDoState } from "../atoms";
import styled from 'styled-components';

const Card = styled.div`
  border-radius: 5px;
  margin-bottom: 10px;
  background-color: black;
  padding: 15px;
`;

const Title = styled.h5`
  margin-bottom: 10px;
`;


const ButtonGroup = styled.div`
  display: flex;
  justify-content: space-between;
`;

const Button = styled.button`
  margin-right: 5px;
  font-size: 12px;
  border: 1px solid #555;
  background-color: #2d384c;
  color: #f1f1f1;
`;

function ToDo({ text, category, id }: IToDo) {
  const currentCategory = useRecoilValue(currentCategoryState);
  const categories = useRecoilValue(categoryState);
  const setToDos = useSetRecoilState(toDoState);
  const onClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    const {
      currentTarget: { name },
    } = event;

    setToDos((oldToDos) => {
      const targetIndex = oldToDos.findIndex((toDo) => toDo.id === id);
      const newToDo = { text, id, category: name as any };
  
      return [
        ...oldToDos.slice(0, targetIndex),
        newToDo,
        ...oldToDos.slice(targetIndex + 1),
      ];
    });
  };

  const handleDelete = () => {
    setToDos((oldToDos) => {
      const newToDos = oldToDos.filter((toDo) => toDo.id !== id);
      return newToDos;
    });
  };

  function notCurrentCategories(cat: ICategory) {
    if (currentCategory !== cat.title) {
      return <Button key={cat.id} name={cat.title} onClick={onClick}>{cat.title}</Button>
     }
  }
  
  return (
    <li key={id}>
      <Card>
        <Title>🍏 {text}</Title>
        <ButtonGroup>
          <div>
            {
              categories?.map(notCurrentCategories)
            }
          </div>
          <Button onClick={handleDelete}>Delete</Button>
        </ButtonGroup>        
      </Card>
    </li>
  );
}

export default ToDo;